<template>
<div >
    <div class="list">
        <input type="text" v-model="newTodo" placeholder="what needs to be done">
        <button @click="addTodo()"><i class="fas fa-plus" style="color:white; font-size: 14px"></i></button>
    </div>
    <div class="todo" >
        <todo
            v-for="(todo, index) in todos"
            :key="index"
            :todo="todo"            
            :deleteTodo="deleteTodo"
            :index="index"
        >
            
        </todo>
    </div>
</div>
    
</template>
<script>
import Todo from './Todo.vue'
export default {
    data(){
        return {
            newTodo: '',
            todos: [ ]
        }
    },
    methods:{
        addTodo(){
            if(this.newTodo != '')
            {
                this.todos.push({
                content: this.newTodo,
                done: false
                });
            }
            
            this.newTodo = '';
        },
        deleteTodo(index){
            this.todos.splice(index,1)
        }
    },
    components:{
        Todo
    }
}
</script>
<style scoped>
.list{    
    margin-top:60px;
    text-align: center;
}
.list input{
    width: 300px;
    padding: 10px;
    outline: none;
    border-radius: 4px 0px 0 4px;
    color: #489CC1;
}
.list button{
    padding: 10px;
    background-color: #489CC1;
    border-radius: 0 4px 4px 0px;
}
.todo{
    margin-top: 20px;
    margin-bottom: 30px;
    
}
</style>