<template>
    <div class="todo1">
        <div class="main">
            <span
            class="checkbox"
            :class="{checked:todo.done}"
            @click="todo.done = !todo.done"
            >        
            </span>
        
            <span 
            class="todo2"
            :class="{done:todo.done}"
            
             >
            {{todo.content.toUpperCase()}}
            
          
            </span>
        </div>
        
        <a href="#" @click.prevent="deleteTodo(index)"><i class="fas fa-trash" style="color:orange"></i></a>
        
        
    </div>
</template>
<script>
export default {
    props:['todo','deleteTodo','index']
}
</script>
<style scoped> 
    div.todo1{
        margin-left: 195px;
        display: flex;
        justify-content: space-between;
        margin-right: 195px;
        align-items: center;
    }
    div.todo1 .main{
        display: flex;
        align-items: center;
    }
    span.todo2{
        display: inline-block;        
        padding:10px;
        height: 22px;
        line-height: 22px;
        font-size: 27px;
    }
    
    span.done {       
        text-decoration: line-through;
        
    }
    .checkbox{
        display: inline-block;
        width: 20px;
        height: 20px;
        border: 1px solid #489CC1;       
        line-height: 20px;
        border-radius: 50%;
    }
    .checkbox.checked{
        background-color: red;
    }
</style>